import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Img} from "core/ui/controls/img"
import {Images} from "games/polyglot/wordsort/assets"
import {Words} from "games/polyglot/wordsort/words"
import * as _ from 'lodash'

export class FolderComponent extends GameComponent<{ right: boolean }> implements OnInit {
  positions = Sizes.create({
    icon: [211, 176],
  })
  img: Img
  folderPapers: Img[]
  folderTop: Img
  nextPaperIndex = 0

  onInit(): void {

    this.addImage(Images.folder(4))
    this.folderPapers = _.range(3).map(i => {
      return this.addImage(Images.folder(3 - i)).fluentUpdate(x => x.visible = false)
    })
    this.folderTop = this.addImage([Images.folder(0), [0, 0]])
    this.img = this.addImage(Images.icon(0, true)).setPos(this.positions.icon).anchorCenter()
  }

  addPaper() {

    if (this.nextPaperIndex >= this.folderPapers.length) {
      return
    }
    this.folderPapers[this.nextPaperIndex].visible = true
    this.nextPaperIndex += 1

  }

  setOnTop() {
    const onTop = [
      this.folderPapers[1],
      this.folderPapers[2],
      this.folderTop,
      this.img
    ]
    onTop.forEach(x => this.bringToTop(x))
  }

  setCategory(type: Words.Category) {
    this.img.key = this.gameKey(Images.icon(type, this.props.right))
  }

}




