import {ILastAnswer} from "core/types/logic"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Tweens} from "core/ui/animations/tweens"
import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {IntroAnimationComponent} from "core/ui/components/games/introAnimationComponent"
import {TextPanelComponent} from "core/ui/components/games/gameUI/textPanelComponent"
import {OnInit} from "core/ui/components/onInit"
import {TextOnCenterBehavior} from "core/ui/controls/behaviors/textOnCenterBehavior"
import {Btn} from "core/ui/controls/btn"
import {Control} from "core/ui/controls/control"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {Img} from "core/ui/controls/img"
import {screenSizeService} from "core/ui/services/instances"
import {Numbers} from "core/utils/numbers"
import {Objects} from "core/utils/objects"
import {Fonts, Images, Sounds, Translations} from "games/polyglot/wordsort/assets"
import {FolderComponent} from "games/polyglot/wordsort/components/folderComponent"
import {Task} from '../logic'

const biValues = BiValue.create({
  arrowVisible: true,
  paperDuration: 500,
  paperScale: 1
}, {
  arrowVisible: false,
  paperDuration: 300,
  paperScale: 0.9
})

const durations = {
  btn: 100
}

export class MainComponent extends BaseMainGameComponent implements OnInit {
  positions = Sizes.create({
    word: {y: 400},
    wordMargin: 80,
    statusOffset: -240,
    paper: {y: -25},
    animation: {y: 20},
    arrow: {x: 160, y: 1040},
  })
  biPositions = Sizes.bi({
    button: {x: 520, y: 1030},
    taskText: {y: 250},
    paperInFolder: {x: [200, 215], y: [-400, 140]},
    taskSize: {width: 900, height: 160},
  }, {
    button: {x: 180, y: 1120},
    taskText: {y: 220},
    paperInFolder: {x: [215], y: [140]},
    taskSize: {width: 700, height: 160},
  })

  bg = this.addImage(Images.bg(0))
  taskText = this.add(new TextPanelComponent({size: this.current(this.biPositions.taskSize)}))
    .setBiPos(BiValue.map(this.biPositions.taskText, ({y}) => ({x: Sizes.w2, y}))).anchorCenter()
    .onBiValueChange(this.biPositions.taskSize, (x, v) => x.changePanel({size: v}))
  paperGroup = this.add(new Control()).anchorCenter().updatePos(Sizes.w2, Sizes.h2)
  paperAll = this.paperGroup.add(this.makeImage(Images.paper_all).anchorCenter()).onBiValueChange(biValues.paperScale, (c, v) => c.symmetricScale = v)
  zaglushka = this.paperGroup.add(this.makeImage(Images.zaglushka).anchorCenter())
  paper: Img
  btns = [this.createButton(false), this.createButton(true)]

  createFolder(right: boolean) {
    return this.createComponentWithProps(FolderComponent, {right})
  }

  createArrow(right: boolean) {
    const pos = Objects.update(this.positions.arrow,
      {x: (x: number) => Sizes.w2 + Numbers.sign(right) * x})
    return this.addImage(Images.arrow(right)).setPos(pos).anchorCenter()
  }

  introAnimation() {
    return this.add(this.createComponent(IntroAnimationComponent))
      .anchorCenter().updatePos(Sizes.w2, Sizes.h2).startAnimation(false)
  }

  createButton(right: boolean) {
    const pos = BiValue.map(this.biPositions.button, ({x, y}) => ({x: Sizes.w2 + Numbers.sign(right) * x, y}))
    const folder = this.createFolder(right)
    const arrow = this.createArrow(right).onBiValueChange(biValues.arrowVisible,
      (x, v) => {
        x.visible = v
      })
    const btn = this.add(new Btn("", () => {
      },
      undefined, folder)).setBiPos(pos).setKey(right ? Phaser.KeyCode.RIGHT : Phaser.KeyCode.LEFT).anchorCenter()
    this.addAnswerBehavior(btn, right, {location: "top"})
    return {folder, btn, arrow}
  }

  onInit(): void {
    this.statusOptions.offset = this.positions.statusOffset
    this.startIntro()
    this.playBackgroundLoop()
  }

  createTaskPaper() {

    if (this.isGameCompleted()) {
      return
    }
    const text = this.getCurrentTask<Task>().word
    const textOnCenter = new TextOnCenterBehavior(Fonts.word, this.positions.wordMargin)
    const paper = this.paperGroup.add(this.makeImage(Images.paper)).updatePos(0, this.positions.paper.y).anchorCenter()
      .addBehavior(textOnCenter).onBiValueChange(biValues.paperScale, (c, v) => c.symmetricScale = v)
    textOnCenter.setText(text)
    this.paper = paper
  }

  onAnswer(answer: ILastAnswer) {
    const arrow = this.btns[answer.answerId ? 1 : 0].arrow
    CommonAnimations.highlight(arrow, {repeat: 0, duration: this.current(biValues.paperDuration) / 2})
    this.animationPaper()
  }

  animationPaper() {
    const oldPaper = this.paper
    this.createTaskPaper()

    if (oldPaper) {
      this.playSound(Sounds.paper)
      this.bringToTop(oldPaper)
      const right = this.prevTask.answers[0]
      const btn = this.btns[right ? 1 : 0]
      const folder = btn.folder
      this.bringToTop(folder)
      ControlUtl.addAndSavePosition(folder, oldPaper, this)
      folder.setOnTop()
      this.btns.forEach(x => this.bringToTop(x))
      const sign = Numbers.sign(right)
      const scale = {symmetricScale: [0.7, 0.32]}
      const to = screenSizeService.isPortrait
        ? this.current(this.biPositions.paperInFolder)
        : {...this.current(this.biPositions.paperInFolder), angle: [sign * -10, sign * 10]}
      Tweens.start({
        target: oldPaper,
        to: {...to, ...scale},
        easing: Phaser.Easing.Sinusoidal.In,
        onComplete: () => {
          folder.addPaper()
          this.animationButton(right)
          oldPaper.destroy(true)
          this.answersAreReady()
        },
        duration: this.current(biValues.paperDuration)
      })
    } else {
      this.answersAreReady()
    }
  }

  prevTask: Task

  animationButton(right: boolean) {
    this.playSound(Sounds.folder)
    const btn = this.btns[right ? 1 : 0].btn
    Tweens.start({
      target: btn,
      to: {y: btn.y + this.positions.animation.y, angle: Numbers.sign(right) * 5},
      easing: Phaser.Easing.Sinusoidal.InOut,
      duration: durations.btn,
      simpleYoyo: true
    })
  }

  onNewTask(task: Task) {
    if (this.isFirstTask) {
      this.createTaskPaper()
      this.onIntroCompleteOrMemorize.addOnce(() => {
        this.answersAreReady()
      })
    }
    const taskText = this.translate(Translations.task) + "\n" +
      this.translate(Translations.category(task.category))
    this.taskText.setProps(taskText)
    this.bg.key = this.gameKey(Images.bg(task.category))
    this.btns.forEach(x => x.folder.setCategory(task.category))
    this.prevTask = task
  }

}



