import {GameLogic} from "core/logic"
import {RandomGenerator} from "core/utils"
import {UniqSeq} from "core/utils/circleSeq"
import {Words} from "games/polyglot/wordsort/words"
import * as _ from 'lodash'
import {Level, levels} from "./levels"

export type Task = {
  word: string
  category: Words.Category
  answers: boolean[]
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()
  wordSeq = _.fromPairs(Words.allCategories.map(x => [x, new UniqSeq(1, this.rng)]))
  categorySeq = new UniqSeq(1, this.rng)

  createTaskData(level: Level) {
    if (this.isTutorial) {
      level = levels[this.taskIndex + 1] as any
    }
    const right = this.rng.bool()
    const category = this.categorySeq.nextFrom(level.category)
    const word = this.wordSeq[category].nextFrom(Words.words[category][right ? 1 : 0])
    const answers = [right]
    return {
      word,
      category,
      answers
    }
  }

  nextTask(level: Level) {
    const task = this.createTaskData(level)
    return task
  }

}





