import {createLevelsFromArray} from "core/types/logic"
import {Words} from "games/polyglot/wordsort/words"

export interface Level {
  category: Words.Category[]
}

function level(category: Words.Category[]): Level {
  return {category}
}

export const levels = createLevelsFromArray([
  level([Words.Category.Food]),
  level([Words.Category.Life]),
  level([Words.Category.Positive]),
  level([Words.Category.Countable]),
  level([Words.Category.Abstract]),
  level([Words.Category.Food, Words.Category.Life]),
  level([Words.Category.Positive, Words.Category.Countable]),
  level([Words.Category.Food, Words.Category.Life, Words.Category.Positive]),
  level([Words.Category.Positive, Words.Category.Countable, Words.Category.Abstract]),
  level([Words.Category.Food, Words.Category.Life, Words.Category.Positive, Words.Category.Countable]),
  level([Words.Category.Life, Words.Category.Positive, Words.Category.Countable, Words.Category.Abstract]),
  level(Words.allCategories),
])



