import {CoreColors, CoreFonts} from "core/assets/fonts"
import {ImagesMetaData} from "core/types"

export module Sounds {
  export const bg = "bg"
  export const folder = "folder"
  export const paper = "paper"
  export const keys = [bg, folder, paper]
}

export module Translations {
  export const task = "task"
  export const category = (id: number) => "category" + id
}

export module Fonts {
  export const word = {
    fontSize: 100, font: CoreFonts.MontserratSemiBold, fill: CoreColors.lightBlack, align: "center"
  }
}

export module Images {
  export const icon = (index: number, right: boolean) => `icon_${index}_${right ? "right" : "left"}`
  export const arrow = (right: boolean) => `arrow_${right ? "right" : "left"}`
  export const folder = (index: number) => "folder_" + index
  export const bg = (index: number) => "bg_" + index
  export const paper = "paper"
  export const zaglushka = "zaglushka"
  export const paper_all = "paper_all"
  export const data: ImagesMetaData = {
    sprites: []
  }

}




